
@use '../../styles/theme/variables' as *;
@use '../../styles/theme/mixins' as *;

.character-panel-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;
  background-color: transparent;
}

.character-sheet {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-state {
  @include flex-center;
  height: 100%;
  font-family: $font-family-title;
  color: $color-gold-pale;
}

.additional-info-drawer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba($color-black-void, 0.8);
  backdrop-filter: blur(5px);
  border-top: 1px solid rgba($color-gold-liu, 0.3);
  transform: translateY(100%);
  transition: transform 0.4s ease-in-out;
  z-index: 100;

  &.is-open {
    transform: translateY(0);
  }

  .drawer-toggle {
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba($color-gold-liu, 0.8);
    color: $color-black-void;
    border: none;
    border-radius: 10px 10px 0 0;
    padding: $spacing-xs $spacing-md;
    cursor: pointer;
    font-family: $font-family-title;
  }

  .drawer-content {
    padding: $spacing-lg;
    padding-top: $spacing-xl;
    display: flex;
    flex-direction: column;
    gap: $spacing-md;
  }

  .info-item {
    display: flex;
    justify-content: space-between;
    font-size: $font-size-base;
    .label {
      color: $color-grey-stone;
    }
    .value {
      color: $color-white-moon;
      font-weight: bold;
    }
  }

  .world-attributes-section {
    margin-top: $spacing-lg;
    padding-top: $spacing-lg;
    border-top: 1px solid rgba($color-gold-liu, 0.2);

    .section-title {
      font-family: $font-family-title;
      color: $color-gold-pale;
      text-align: center;
      margin-bottom: $spacing-md;
    }

    .attributes-list {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-direction: column;
      gap: $spacing-sm;
    }

    .attribute-item {
      display: flex;
      justify-content: space-between;
      font-size: 0.9em;
      .label {
        color: $color-grey-stone;
      }
      .value {
        color: $color-white-moon;
        font-weight: bold;
      }
    }

    .empty-state {
      text-align: center;
      color: $color-grey-stone;
      font-style: italic;
    }
  }
}
