
@use '../../styles/theme/variables' as *;

.npc-directory-panel {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  height: 100%;
  position: relative;
  background: $color-black-void;
  color: $color-white-moon;
}
.search-input {
  width: 100%;
  padding: $spacing-sm;
  margin-bottom: $spacing-md;
  background-color: rgba($color-black-void, 0.5);
  border: 1px solid $color-grey-stone;
  color: $color-white-moon;
  border-radius: $border-radius-sm;
}
.world-list-overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 250px;
  background-color: rgba(10, 15, 30, 0.85);
  backdrop-filter: blur(8px);
  z-index: 10;
  border-right: 1px solid rgba($color-gold-liu, 0.3);
  padding: $spacing-md;
}
.world-list-column ul,
.npc-list {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    padding: $spacing-sm $spacing-md;
    cursor: pointer;
    border-radius: $border-radius-sm;
    transition: background-color 0.2s ease;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:hover {
      background-color: rgba($color-cyan-tian, 0.1);
    }
    &.active {
      background-color: $color-cyan-tian;
      color: $color-black-void;
      font-weight: bold;
    }
  }
}
.toggle-world-list {
  position: absolute;
  top: 12px;
  left: 12px;
  z-index: 20;
  background: rgba($color-black-void, 0.7);
  border: 1px solid $color-gold-liu;
  color: $color-gold-liu;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  font-family: $font-family-title;
}
[class*='-column'] {
  padding: $spacing-md;
  overflow-y: auto;
  border-right: 1px solid rgba($color-gold-liu, 0.15);
  &:last-child {
    border-right: none;
  }
  h4 {
    font-family: $font-family-title;
    color: $color-gold-pale;
    text-align: center;
    margin-top: 0;
    margin-bottom: $spacing-lg;
    padding-bottom: $spacing-md;
    border-bottom: 1px solid rgba($color-gold-liu, 0.3);
  }
}
.npc-info {
  display: flex;
  flex-direction: column;
}
.npc-location {
  font-size: $font-size-small;
  color: $color-grey-stone;
  margin-top: 4px;
}
