
@use '../styles/theme/variables' as *;
@use '../styles/theme/mixins' as *;

.top-status {
  @include frosted-glass(rgba($color-charcoal-glass, 0.5), 8px);
  border-bottom: 1px solid rgba($color-gold-liu, 0.2);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: $spacing-sm $spacing-md;
  position: relative;
  z-index: 100;

  // Add a subtle inner glow to give it a "control panel" feel
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    border-radius: inherit;
    box-shadow: inset 0 1px 1px rgba(lighten($color-gold-liu, 20%), 0.1);
  }
}

// Placeholder for future content
// We will add info items here later

.status-left,
.status-right {
  flex: 1;
  display: flex;
  align-items: center;
  gap: $spacing-md;
}

.status-left {
  justify-content: flex-start;
}

.status-right {
  justify-content: flex-end;
}

.status-center {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: $spacing-xl;
}

.view-toggle-btn {
  background: none;
  border: 1px solid transparent;
  color: $color-grey-stone;
  font-size: 18px;
  padding: $spacing-xs $spacing-sm;
  border-radius: $border-radius-sm;
  cursor: pointer;
  transition: all 0.2s ease-in-out;

  &:hover {
    color: $color-white-moon;
    background-color: rgba($color-gold-liu, 0.1);
    border-color: rgba($color-gold-liu, 0.3);
  }
}
