
.avatar-selection {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.avatar-card {
  border: 1px solid #ccc;
  padding: 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    border-color: #007bff;
    box-shadow: 0 0 10px rgba(0, 123, 255, 0.5);
  }

  h4 {
    margin-top: 0;
  }
}
