
@use '../../../styles/theme/variables' as *;
@use '../../../styles/theme/mixins' as *;

.world-selection-container {
  // Ensure the container itself doesn't create scrollbars
  overflow: hidden;
}

.star-map-canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  cursor: grab;
  &:active {
    cursor: grabbing;
  }
}

.unselectable-notice {
  text-align: center;
  color: $color-grey-stone;
  font-style: italic;
}

.map-controls {
  position: absolute;
  bottom: 20px;
  right: 20px; /* Aligned to the right */
}

.speed-controls-overlay {
  position: absolute;
  top: 50%;
  left: 30px;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  z-index: 10;

  .control-btn {
    position: relative;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid rgba($color-gold-liu, 0.4);
    color: $color-gold-pale;
    font-size: 24px;
    line-height: 1;
    cursor: pointer;
    transition: all 0.3s ease;
    @include flex-center;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80%;
      height: 80%;
      border-radius: 50%;
      border: 1px solid rgba($color-cyan-tian, 0.5);
      transition: all 0.3s ease;
    }

    &:hover {
      border-color: $color-gold-liu;
      color: $color-white-moon;
      box-shadow: 0 0 10px rgba($color-gold-liu, 0.5);

      &::before {
        transform: translate(-50%, -50%) scale(1.2);
        opacity: 0;
      }
    }
  }

  .speed-bar-container {
    width: 8px;
    height: 100px;
    background: rgba($color-black-void, 0.5);
    border-radius: 4px;
    position: relative;
    border: 1px solid rgba($color-gold-liu, 0.2);
    overflow: hidden;
    display: flex;
    flex-direction: column-reverse; /* Fill from bottom */
  }

  .speed-bar-fill {
    width: 100%;
    background: linear-gradient(to top, $color-cyan-tian, $color-gold-pale);
    transition: height 0.3s ease;
  }
}

.epoch-tabs {
  display: flex;
  gap: $spacing-sm;
  margin-bottom: $spacing-md;

  button {
    padding: $spacing-xs $spacing-md;
    border: 1px solid rgba($color-cyan-tian, 0.3);
    background-color: transparent;
    color: $color-grey-stone;
    border-radius: $border-radius-sm;
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover {
      background-color: rgba($color-cyan-tian, 0.1);
      border-color: $color-cyan-tian;
      color: $color-white-moon;
    }

    &.active {
      background-color: $color-cyan-tian;
      border-color: $color-cyan-tian;
      color: $color-black-void;
      font-weight: bold;
    }
  }
}

.detail-item {
  ul {
    list-style-type: none;
    padding-left: $spacing-md;
    margin: $spacing-sm 0 0 0;
  }

  li {
    color: $color-white-moon;
    font-size: $font-size-small;
    line-height: 1.6;
  }
}
