.inventory-panel{padding:24px;height:100%;overflow:hidden;container-type:inline-size}.inventory-layout{display:grid;grid-template-columns:1fr;height:100%;width:100%;position:relative}@container (min-width: 600px){.inventory-layout{grid-template-columns:1fr 0;gap:24px;transition:grid-template-columns .5s ease-in-out}.inventory-layout.detail-open{grid-template-columns:2fr 1fr}}.main-content{display:flex;flex-direction:column;min-width:0}.talisman-tabs{display:none}.item-grid-container{flex-grow:1;overflow-x:auto;overflow-y:hidden}.item-grid-container::-webkit-scrollbar{width:6px;height:6px}.item-grid-container::-webkit-scrollbar-track{background:rgba(0,0,0,0);border-radius:3px}.item-grid-container::-webkit-scrollbar-thumb{background:#d4af37;border-radius:3px;transition:background .3s ease}.item-grid-container::-webkit-scrollbar-thumb:hover{background:rgb(221.024691358,191.7901234568,96.975308642)}.item-grid-container{padding:24px;background:url("https://www.transparenttextures.com/patterns/old-paper.png");background-color:#e8e0d0;border-radius:5px;border:1px solid #c9bba8}.item-grid{display:flex;align-items:center;flex-wrap:nowrap;gap:4cqi;padding-bottom:24px;min-width:min-content}.chapter-separator{display:flex;justify-content:center;align-items:center;flex-shrink:0;width:12cqi;height:12cqi;min-width:50px;min-height:50px;background:linear-gradient(145deg, #a03030, #d85050);border:max(2px,.3cqi) solid #5a2020;border-radius:.8cqi;box-shadow:inset 0 0 1.5cqi rgba(0,0,0,.5),0 .3cqi .6cqi rgba(0,0,0,.3);position:relative}.chapter-separator::after{content:"";position:absolute;inset:0;background-image:url("https://www.transparenttextures.com/patterns/subtle-grunge.png");opacity:.3;mix-blend-mode:overlay;border-radius:.4cqi}.chapter-separator .text{font-family:"ZCOOL KuaiLe","",cursive;font-weight:700;font-size:clamp(1.8rem,4.5cqi,3rem);color:#e0d0b8;text-shadow:1px 1px 1px hsla(0,0%,100%,.15),-1px -1px 1px rgba(0,0,0,.5)}.item-cell{flex-shrink:0;width:15cqi;height:15cqi;min-width:60px;min-height:60px;border-radius:50%;display:flex;justify-content:center;align-items:center;text-align:center;cursor:pointer;transition:all .3s ease;position:relative;background-color:#b8d8c8;background-image:radial-gradient(ellipse 60% 40% at 75% 80%, rgba(100, 140, 120, 0.3) 0%, transparent 100%),radial-gradient(ellipse 30% 20% at 20% 25%, rgba(230, 255, 240, 0.2) 0%, transparent 100%),radial-gradient(ellipse 25% 15% at 40% 55%, rgba(210, 230, 220, 0.15) 0%, transparent 100%),radial-gradient(ellipse 80% 70% at 50% 40%, rgba(240, 255, 245, 0.25) 0%, transparent 70%),linear-gradient(155deg, #d0e8e0, #a8c8b8);box-shadow:inset .2cqi .3cqi .8cqi hsla(0,0%,100%,.4),inset -0.3cqi -0.6cqi 1.5cqi rgba(40,80,60,.3),inset 0 0 3cqi rgba(184,216,200,.5),0 .8cqi 2cqi rgba(0,0,0,.2);border:max(1px,.15cqi) solid hsla(0,0%,100%,.5);mask-image:radial-gradient(circle, transparent 8%, black 8.5%);-webkit-mask-image:radial-gradient(circle, transparent 8%, black 8.5%)}.item-cell::before,.item-cell::after{content:"";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:16%;height:16%;border-radius:50%;z-index:-1}.item-cell::before{box-shadow:0 .1cqi .3cqi rgba(20,40,30,.4)}.item-cell::after{box-shadow:0 -0.1cqi .3cqi hsla(0,0%,100%,.5)}.item-cell .item-name-arc{position:absolute;inset:0;width:100%;height:100%;overflow:visible}.item-cell .item-name-arc .arc-text{font-family:"Noto Serif SC",serif;font-size:clamp(10px,1.4cqi,13px);font-weight:500;fill:#184830;letter-spacing:.2cqi}.item-cell .item-name{display:none}.item-cell .item-footer{position:absolute;bottom:1.5cqi;right:1.5cqi;background:none;font-size:clamp(.9rem,1.8cqi,1.2rem);font-weight:bold;color:rgba(8,36,22,.6);text-shadow:1px 1px 1px hsla(0,0%,100%,.5);z-index:2}.item-cell:hover{transform:scale(1.1) translateY(-0.5cqi);box-shadow:inset .2cqi .3cqi 1.5cqi hsla(0,0%,100%,.7),inset -0.4cqi -0.8cqi 2cqi rgba(80,120,100,.5),inset 0 0 4cqi rgba(184,216,200,.7),0 2cqi 4cqi rgba(var(--tier-color-rgb), 0.4)}.scroll-detail-view{opacity:0;transform:translateX(20px);transition:opacity .5s ease-in-out,transform .5s ease-in-out;overflow:hidden;position:relative;background:rgba(0,0,0,0)}.inventory-layout.detail-open .scroll-detail-view{opacity:1;transform:translateX(0)}.scroll-detail-view .scroll-content{background-color:#0a0f1e;border:1px solid rgba(212,175,55,.5);border-radius:5px;height:100%;padding:24px;overflow-y:auto}.scroll-detail-view .scroll-content::-webkit-scrollbar{width:6px;height:6px}.scroll-detail-view .scroll-content::-webkit-scrollbar-track{background:rgba(0,0,0,0);border-radius:3px}.scroll-detail-view .scroll-content::-webkit-scrollbar-thumb{background:#d4af37;border-radius:3px;transition:background .3s ease}.scroll-detail-view .scroll-content::-webkit-scrollbar-thumb:hover{background:rgb(221.024691358,191.7901234568,96.975308642)}.scroll-detail-view .close-button{position:absolute;top:16px;right:16px;background:none;border:none;font-size:clamp(1.5rem,4vw,2rem);line-height:1;color:#a0a0a0;cursor:pointer;transition:color .3s ease,transform .3s ease;z-index:2}.scroll-detail-view .close-button:hover{color:#e0e0e0;transform:scale(1.2)}.scroll-detail-view .detail-name{font-family:"ZCOOL KuaiLe","",cursive;font-size:clamp(1.5rem,5vw,2.2rem);color:#f0e6d2;text-align:center;margin-bottom:8px;text-shadow:0 0 10px rgba(var(--tier-color-rgb), 0.9),0 0 20px rgba(var(--tier-color-rgb), 0.6)}.scroll-detail-view .detail-meta{display:flex;justify-content:center;gap:16px;margin-bottom:24px;font-size:clamp(.8rem,2.5vw,1rem);color:#a0a0a0}.scroll-detail-view .detail-meta .meta-tier{color:#3a8f9d;font-weight:bold;text-shadow:0 0 5px #3a8f9d}.scroll-detail-view .detail-meta .meta-type{font-style:italic}.scroll-detail-view .detail-desc{font-family:"Siyuan Song","Source Han Serif SC",serif;line-height:1.8;color:#f0f0f0;text-shadow:1px 1px 3px rgba(0,0,0,.7);margin-bottom:24px;font-size:clamp(.8rem,2.5vw,1rem)}.scroll-detail-view .detail-effects{margin-top:24px;border-top:1px solid rgba(212,175,55,.3);padding-top:24px}.scroll-detail-view .detail-effects h5{font-family:"ZCOOL KuaiLe","",cursive;font-weight:normal;font-size:clamp(1.1rem,4vw,1.5rem);color:#f0e6d2;margin-bottom:16px;text-shadow:1px 1px 2px rgba(0,0,0,.5)}.scroll-detail-view .detail-effects ul{padding-left:24px;list-style:" ";color:#e8e8e8;text-shadow:1px 1px 3px rgba(0,0,0,.6);font-size:clamp(.8rem,2.5vw,.95rem)}.scroll-detail-view .detail-effects ul li{margin-bottom:8px}.scroll-detail-view .detail-effects ul li .effect-tag{color:#3a8f9d;font-weight:bold;margin-right:8px}.scroll-detail-view .detail-effects ul li .effect-type{color:#a0a0a0;font-style:italic;margin-right:4px}.scroll-detail-view .detail-effects ul li .effect-value{color:#f0e6d2;font-weight:bold}