
@use '../styles/theme/variables' as *;
@use '../styles/theme/mixins' as *;

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @include frosted-glass(rgba($color-black-void, 0.8), 10px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.loading-content {
  background-color: $color-indigo-deep;
  border: 1px solid rgba($color-gold-liu, 0.5);
  border-radius: $border-radius-md;
  padding: $spacing-xl;
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: $spacing-lg;
  width: 80%;
  max-width: 500px;
  text-align: center;

  .tip {
    font-family: $font-family-main;
    font-size: $font-size-large;
    color: $color-gold-pale;
    line-height: 1.6;
    text-shadow: 0 0 5px $color-gold-pale;
  }

  .progress-bar-container {
    width: 100%;
    height: 12px;
    background-color: rgba($color-black-void, 0.5);
    border-radius: 6px;
    border: 1px solid rgba($color-gold-liu, 0.4);
    overflow: hidden;
  }

  .progress-bar {
    height: 100%;
    background: linear-gradient(90deg, $color-cyan-tian, $color-gold-liu);
    transition: width 0.2s linear;
    border-radius: 6px;
  }

  .progress-text {
    font-family: $font-family-title;
    font-size: $font-size-base;
    color: $color-grey-stone;

    .highlight {
      color: $color-gold-pale;
      font-weight: bold;
      text-shadow: 0 0 5px rgba($color-gold-liu, 0.7);
    }
  }

  .error-container {
    .error-title {
      color: $color-red-chi;
      font-family: $font-family-title;
      font-size: $font-size-h1;
      margin-bottom: $spacing-md;
      text-shadow: 0 0 8px rgba($color-red-chi, 0.6);
    }
    .error-message {
      color: $color-white-moon;
      font-size: $font-size-base;
      margin-bottom: $spacing-lg;
    }
    .error-tip {
      color: $color-grey-stone;
      font-size: $font-size-small;
    }
  }
}
