
@use '../../styles/theme/variables' as *;
@use '../../styles/theme/mixins' as *;

.inventory-panel {
  padding: $spacing-lg;
  height: 100%;
  overflow: hidden;
}

.inventory-layout {
  display: grid;
  grid-template-rows: auto 1fr;
  gap: $spacing-lg;
  height: 100%;
  width: 100%;
}

.category-header {
  flex-shrink: 0;
  background-color: rgba($color-black-void, 0.3);
  border-radius: $border-radius-md;
  padding: $spacing-sm;

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    gap: $spacing-sm;
  }

  li {
    padding: $spacing-sm $spacing-md;
    cursor: pointer;
    border-radius: $border-radius-sm;
    transition: background-color 0.3s ease;
    color: $color-grey-stone;

    &:hover {
      background-color: rgba($color-cyan-tian, 0.1);
    }

    &.active {
      background-color: rgba($color-cyan-tian, 0.2);
      color: $color-cyan-tian;
      font-weight: bold;
    }
  }
}

.main-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: $spacing-lg;
  min-width: 0;
  min-height: 0;
}

.item-grid-container {
  overflow-y: auto;
  @include custom-scrollbar;
}

.item-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: $spacing-md;
}

.item-cell {
  aspect-ratio: 1;
  border: 1px solid var(--tier-border-color, #{$color-grey-stone});
  background-color: rgba($color-black-void, 0.5);
  border-radius: $border-radius-sm;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: $spacing-sm;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
  }

  &.selected {
    transform: translateY(-2px);
    box-shadow: 0 0 15px var(--tier-border-color);
    border-width: 2px;
  }

  .item-name {
    font-size: $font-size-base;
    color: $color-white-moon;
    text-align: center;
  }

  .item-quantity {
    font-size: $font-size-small;
    color: $color-grey-stone;
    text-align: right;
  }
}

.empty-grid {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: $color-grey-stone;
  font-style: italic;
}
