.log-panel-container{height:100%;overflow-y:auto;padding:24px}.log-panel-container::-webkit-scrollbar{width:6px;height:6px}.log-panel-container::-webkit-scrollbar-track{background:rgba(0,0,0,0);border-radius:3px}.log-panel-container::-webkit-scrollbar-thumb{background:#d4af37;border-radius:3px;transition:background .3s ease}.log-panel-container::-webkit-scrollbar-thumb:hover{background:rgb(221.024691358,191.7901234568,96.975308642)}.log-panel-container{background-color:rgba(0,0,0,0)}.timeline{position:relative;margin:0 auto;padding:24px 0;width:100%;list-style:none}.timeline::before{content:"";position:absolute;top:0;left:20px;height:100%;width:2px;background:rgba(212,175,55,.2)}.timeline-item{margin-bottom:32px;position:relative;padding-left:55px}.timeline-item:last-child{margin-bottom:0}.timeline-marker{position:absolute;top:4px;left:12px;width:18px;height:18px;border-radius:50%;background:#16212c;border:3px solid #3a8f9d;z-index:1;box-shadow:0 0 10px rgba(58,143,157,.7)}.timeline-content{position:relative;background-color:rgba(22,33,44,.6);border:1px solid rgba(212,175,55,.3);border-radius:5px;padding:16px;transition:all .3s ease;cursor:pointer;backdrop-filter:blur(8px)}.timeline-content:hover{border-color:rgba(240,230,210,.7);background-color:rgba(22,33,44,.8);box-shadow:0 5px 25px rgba(10,15,30,.6)}.log-header{display:flex;justify-content:space-between;align-items:flex-start;margin-bottom:8px}.log-title{font-family:"ZCOOL KuaiLe","",cursive;font-size:16px;color:#f0e6d2;font-weight:700}.log-date{font-size:12px;color:#a0a0a0;white-space:nowrap;margin-left:16px}.log-meta{font-size:12px;color:#a0a0a0;margin-bottom:16px;display:flex;flex-wrap:wrap;gap:16px}.log-meta span{display:flex;align-items:center}.log-meta span .icon{margin-right:4px;color:#3a8f9d}.log-description{font-size:14px;line-height:1.7;color:#d3d3d3;margin:0}.log-details{margin-top:16px;padding-top:16px;border-top:1px dashed rgba(212,175,55,.2);animation:fadeIn .5s ease}.log-details .detail-section{margin-bottom:8px}.log-details .detail-section strong{display:block;color:#f0e6d2;margin-bottom:4px;font-family:"Siyuan Song","Source Han Serif SC",serif}.log-details .detail-section p,.log-details .detail-section pre{background-color:rgba(10,15,30,.4);padding:8px;border-radius:3px;color:hsl(0,0%,77.7450980392%);font-size:12px;white-space:pre-wrap;word-break:break-word;line-height:1.6}.log-tags{margin-top:16px;display:flex;flex-wrap:wrap;gap:8px}.log-tags .tag{display:inline-block;padding:.25em .6em;font-size:75%;font-weight:700;line-height:1;text-align:center;white-space:nowrap;vertical-align:baseline;border-radius:5px;background-color:rgba(58,143,157,.1);color:#3a8f9d}.empty-state{text-align:center;padding:32px;color:#a0a0a0}@keyframes fadeIn{from{opacity:0;transform:translateY(-10px)}to{opacity:1;transform:translateY(0)}}