
@use './styles/theme/variables' as *;
@use './styles/theme/mixins' as *;

.guixu-root-container {
  display: grid;
  grid-template-rows: auto 1fr;
  width: 100%;
  max-height: 100%;
  aspect-ratio: 16 / 13;
  margin: auto;
  overflow: hidden;
  border: 1px solid rgba($color-gold-liu, 0.2);
  border-radius: 12px;
  box-shadow: 0 8px 35px rgba($color-black-void, 0.5);
  background: $color-black-void;
  position: relative;
}

.web-fullscreen {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  aspect-ratio: auto !important;
  overflow: visible !important; /* Allow inner content to scroll in fullscreen */
  border-radius: 0 !important;
  border: none !important;
  z-index: 1;
}

.main-layout {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  overflow: hidden;

  & > * {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
  }
}

// Global Modal Styles
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color-black-void, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 90000;
}

.modal-content {
  @include frosted-glass(rgba($color-indigo-deep, 0.8), 12px);
  border: 1px solid rgba($color-gold-liu, 0.4);
  border-radius: $border-radius-md;
  padding: $spacing-lg;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  min-width: 320px;
  max-width: 500px;

  .confirmation-title {
    color: $color-gold-pale;
    font-family: $font-family-title;
    font-size: $font-size-h2;
    text-align: center;
    margin-bottom: $spacing-md;
  }

  .confirmation-message {
    color: $color-white-moon;
    font-size: $font-size-base;
    line-height: $line-height-base;
    margin-bottom: $spacing-xl;
    text-align: center;
  }

  .confirmation-buttons {
    display: flex;
    justify-content: flex-end;
    gap: $spacing-md;
  }
}

.detail-modal {
  width: 800px; /* Fixed width */
  max-width: 90vw;
  height: 600px; /* Fixed height */
  max-height: 80vh;
  display: flex;
  flex-direction: column;

  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: $spacing-md;
    margin-bottom: $spacing-lg;
    border-bottom: 1px solid rgba($color-gold-liu, 0.3);

    .modal-title {
      color: $color-gold-pale;
      font-family: $font-family-title;
      font-size: $font-size-h2;
    }

    .close-button {
      background: none;
      border: none;
      color: $color-grey-stone;
      font-size: 24px;
      cursor: pointer;
      &:hover {
        color: $color-white-moon;
      }
    }
  }

  .modal-body {
    overflow-y: auto;
    padding-right: $spacing-sm; // for scrollbar
    color: $color-white-moon; // Ensure default text is readable
    flex-grow: 1;
    min-height: 0;
  }
}

.input-modal-overlay {
  align-items: flex-end;
  padding-bottom: 5vh;
  background-color: transparent;
  backdrop-filter: none;
  pointer-events: none;
}

.input-modal-content {
  pointer-events: auto;
  @include frosted-glass(rgba($color-indigo-deep, 0.8), 12px);
  border: 1px solid rgba($color-gold-liu, 0.4);
  border-radius: $border-radius-md;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  padding: 0;
  width: 70%;
  max-width: 900px;
}

.input-area-container {
  position: relative;
}

.input-area-with-buttons {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
}

/* Activator button styles are now moved to EnhancedInputBox.vue */

.action-panel {
  position: absolute;
  bottom: calc(100% + #{$spacing-md});
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.command-queue-popup,
:deep(.input-history-popup) {
  position: absolute;
  bottom: calc(100% + #{$spacing-md});
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  max-width: 600px;
  z-index: 20;
  /* Styles are now applied inline to ensure they are not overridden */
  border-radius: $border-radius-md;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  padding: $spacing-md;
  display: flex;
  flex-direction: column;
  gap: $spacing-md;

  .popup-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: $spacing-sm;
    border-bottom: 1px solid rgba($color-gold-liu, 0.2);

    h3 {
      color: $color-gold-pale;
      font-family: $font-family-title;
      font-size: $font-size-h3;
      margin: 0;
    }

    .close-popup-button {
      background: none;
      border: none;
      color: $color-grey-stone;
      font-size: 22px;
      cursor: pointer;
      line-height: 1;

      &:hover {
        color: $color-white-moon;
      }
    }
  }

  .popup-body {
    .command-list {
      max-height: 200px;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
      gap: $spacing-sm;
      padding-right: $spacing-sm; // for scrollbar
    }

    .command-item {
      background-color: rgba($color-black-void, 0.3);
      padding: $spacing-sm $spacing-md;
      border-radius: $border-radius-sm;
      font-family: $font-family-main;
      color: $color-white-moon;
      word-break: break-all;
    }

    .empty-queue {
      text-align: center;
      color: $color-grey-stone;
      padding: $spacing-lg 0;
    }
  }

  .popup-footer {
    display: flex;
    justify-content: flex-end;
    padding-top: $spacing-sm;
    border-top: 1px solid rgba($color-gold-liu, 0.2);
  }
}

:global(html.web-fullscreen-active),
:global(body.web-fullscreen-active) {
  overflow: hidden !important;
}

:global(body.web-fullscreen-active #chat) {
  padding: 0 !important;
  margin: 0 !important;
  height: 100vh !important;
  max-height: 100vh !important;
}

:global(body.web-fullscreen-active #chat > div) {
  padding: 0 !important;
  margin: 0 !important;
}

:global(body.web-fullscreen-active .mes_block) {
  padding: 0 !important;
  margin: 0 !important;
}

:global(body.web-fullscreen-active .mes_text) {
  padding: 0 !important;
  margin: 0 !important;
}
